<template>
	<view class="list_link">
		<view class="row">
			<view v-for="(o, i) in list" :key="i" :class="'col-' + col">
				<navigator :to="o[vm.url]" class="link">
					<image style="width: 100%; height: auto;" :src="$fullUrl(o[vm.img])" mode="widthFix"></image>
					<view><text>{{ o[vm.title] }}</text></view>
				</navigator>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						img: "img",
						title: "title",
						url: "url"
					}
				}
			},
			span: {
				type: Number,
				default: 3
			}
		},
		data() {
			var col = 12 / this.span;
			return {
				col
			}
		}
	}
</script>

<style scoped>
	.row {
		justify-content: start;
	}

	.link {
		padding: .75rem;
	}
</style>
